      {
        "@type": "HowToStep",
        "text": "Junta-se a 300 g de arroz crioulo (cozido em bastante gua e passado depois for gua fria) 3 colheres de sopa de azeite, sumo de 1 limo, sal, pimenta e bastante salsa picada (a colher bem cheia).\r"
      },
      {
        "@type": "HowToStep",
        "text": "Cozem-se os polvos em pouca gua, com cebola, cerca de 20 minutos (quando a cebola estiver cozida o polvo tambm est).\r"
      },
      {
        "@type": "HowToStep",
        "text": "O polvo tem-se primeiro em gua e depois tira-se-lhe a pele.\r"
      },
      {
        "@type": "HowToStep",
        "text": "Guarda-se a coroa de tentculos de um polvo pequeno (200 g) para guarnecer, depois de cozido. O resto (cerca de 500g) parte-se aos bocadinhos pequenos.\r"
      },
      {
        "@type": "HowToStep",
        "text": "Faz-se uma maionese com 2 gemas e 2dl de leo. Junta-se 1kg de berbigo e o polvo.\r"
      },
      {
        "@type": "HowToStep",
        "text": "Pe-se no meio de um prato redondo, cobre-se com rodelas de ovo cozido (2) e depois com o arroz. Coloca-se o polvo, aberto, por cima e entre os tentculos, umas gambas.\r"
      }
